import http from "../http-common"

class TypePersonDataService 
{
    getAll(): Promise<any> {
        return http.get('/person_type');
    }

    /* este metodo actualiza y crea esta raro revisar back */
    create(data: object): Promise<any> {
        return http.post('/person_type',data)
    }

    delete(id: number){
        return http.delete(`/person_type/${id}`)
    }

    

}

export default new TypePersonDataService();