<template>
  
    <Card>
        <template #title> Tipos de Persona </template>
        <template #content>
            <DataTable v-model:filters="filters" :value="list" paginator :rows="5" :rowsPerPageOptions="[5, 10, 20, 50]" >
                <template #header>
                    <div class="flex justify-content-between">
                        <!-- <Button type="button" label="Nuevo" icon="duotone icon-plus-circle"  /> -->
                        <Button type="button" label="Nuevo" icon="pi pi-plus" @click="newItem" />
                        <span class="p-input-icon-left">
                            <i class="bx bx-search-alt-2" />
                            <InputText v-model="filters['global'].value" placeholder="Buscar" />
                        </span>
                    </div>
                </template>
                
                <Column field="name" header="Nombre"></Column>
                <Column field="state" header="Estado">
                    <template #body="slotProps">
                        <Tag :value="slotProps.data.state ? 'Activo':'Inactivo'" :severity="slotProps.data.state ? 'success':'secondary'" />
                        
                    </template>
                </Column>
                <Column field="actions" header="">
                    <template #body="slotProps">
                        <div class="flex">
                            <Button icon="bx bxs-pencil" style="font-size: 1.2rem" severity="info" text rounded aria-label="User" @click="editItem(slotProps.data)" />
                            <Button icon="bx bxs-trash-alt"  style="font-size: 1.2rem" severity="danger" text rounded aria-label="Cancel" @click="deleteItem(slotProps.data)" />
                        </div>
                    </template>
                </Column>
            </DataTable>
        </template>
    </Card>

    <Dialog v-model:visible="dialog" modal :header="item.id?'Editar':'Nuevo'" :style="{ width: '50vw' }">
        
            <div class="field ">
                <label for="firstname1">Nombre</label>
                <InputText type="text" v-model="item.name" class="w-full" />
            </div>
            <div class="field flex align-content-center">
                <label for="firstname1" class="pr-2">{{ item.state?'Activo':'Inactivo' }} </label>
                <InputSwitch v-model="item.state" />
                <!-- <InputText type="text" v-model="item.name" class="w-full" /> -->
            </div>
        
        <template #footer>
            
            <Button label="Cancelar" icon="pi pi-times" @click="dialog = false" text />
            
            <Button label="Guardar" icon="pi pi-check" @click="saveItem"  />
        </template>
    </Dialog>

</template>
<script setup>
import { onMounted, ref } from 'vue';
import TypePersonDataService from '@/services/type_person.ds'
import { useToast } from "primevue/usetoast";
import { FilterMatchMode, FilterOperator } from 'primevue/api';
import { useConfirm } from "primevue/useconfirm";

const confirm = useConfirm();
const toast = useToast();

const list= ref([])

const dialog = ref(false)

const filters = ref( {
        global: { value: null, matchMode: FilterMatchMode.CONTAINS },
        name: { operator: FilterOperator.AND, constraints: [{ value: null, matchMode: FilterMatchMode.CONTAINS }] }
    })
const item = ref({})


const newItem = (()=>{
    dialog.value = true
    item.value = {}
}) 

const editItem = ((row)=>{
    dialog.value = true
    item.value = row
})

const saveItem = (async()=>{
    try {
        let data = item.value
        data.state = item.value.state ?? false
        let response = await TypePersonDataService.create(data)
        if(response.status === 201)
        {
                toast.add({ severity: 'success', summary: 'Registro Completado', detail: `Se registro "${response.data.name}"`, life: 3000 });
            //    console.log(response)
        }

        if(response.status === 404 || response.status === 500)
        {
            toast.add({ severity: 'error', summary: 'Error', detail: 'Favor de contactarse con el soporte tecnico', life: 3000 });
        }
        dialog.value = false
        getList()
    } catch (error) {
        toast.add({ severity: 'error', summary: 'Error', detail: 'Error de conexion', life: 3000 });
    }
})

const deleteItem = (row) => {
    item.value = row
    confirm.require({
        message: 'Esta de acuerdo con eliminar el registro?',
        header: 'Confirmacion de Eliminacion',
        icon: 'pi pi-info-circle',
        acceptClass: 'p-button-danger',
        acceptLabel: 'Si',
        accept: async () => {
             try {
                 let response = await TypePersonDataService.delete(item.value.id)

                    if(response.status === 200) {
                        getList()
                        toast.add({ severity: 'info', summary: 'Confirmacion', detail: 'Se elimino el registro', life: 3000 });
                    }
                
             } catch (error) {
                toast.add({ severity: 'error', summary: 'Error', detail: 'no se pudo eliminar el registro ', life: 3000 });    
             }
        },
        reject: () => {
            // toast.add({ severity: 'error', summary: 'Rejected', detail: 'You have rejected', life: 3000 });
        }
    })
}

const getList = (async () => {
    try {
        
        /* aqui obtenemos la lista del backend a traves del TypePersonDataService */
        let response  = await TypePersonDataService.getAll()
        //validar con status 200 
        if(response.status === 200)
        {
            list.value = response.data
            // console.log(response)
        }
        
        if(response.status === 404 || response.status === 500)
        {
            toast.add({ severity: 'error', summary: 'Error', detail: 'Favor de contactarse con el soporte tecnico', life: 3000 });
        }

    } catch (error) {
        toast.add({ severity: 'error', summary: 'Error', detail: 'Error de conexion', life: 3000 });
    }

})

onMounted (()=>{
    getList()
   
})

</script>